.App {
  font-family: sans-serif;
  text-align: center;
}

@media (max-width: 499px) {
  .container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 20px;
  }
}

@media (min-width: 500px) {
  .container {
    display: flex;
    margin-top: 20px;
  }
}

.group {
  margin: 10px;
}

.group,
input {
  display: flex;
  justify-content: flex-end;
}

input {
  border-radius: 4px;
  padding: 5px 15px;
  border: 1px solid #cecece;
  width: 150px;
  margin-left: 20px;
}

.whatsapp-img {
  margin-right: 20px;
}

button {
  cursor: pointer;
  background: #cecece;
  border: 1px solid #cecece;
  border-radius: 8px;
  padding: 10px;
  margin-right: 5px;
}

.button-group {
  margin-top: 20px;
}
